<template>
  <div>
    <header-return></header-return>
    <div class="form-wrapper">
      <div class="form-contents">
        <Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="70">
          <FormItem label="名字" prop="name">
            <Input type="text" v-model="formCustom.name"/>
          </FormItem>
          <FormItem label="身份证" prop="identity">
            <Input type="text" v-model="formCustom.identity"/>
          </FormItem>
          <FormItem label="手机号" prop="phone">
            <Input type="text" v-model="formCustom.phone"/>
          </FormItem>
          <FormItem label="验证码" prop="code" class="code-wrap">
            <Row>
              <i-col span="12" style="margin-right: 10px"><Input type="text" v-model="formCustom.code"/></i-col>
              <i-col span="3" class="btn-sms" v-show="!isChecked">
                <input class="code" type="button" ref="btn" value="获取验证码" @click="sendSms"/></i-col>
              <i-col span="1" class="icon-checkmark" v-show="isChecked">
                <Icon type="ios-checkmark" size="18" color="green" style="margin-top: 8px"></Icon>
              </i-col>
            </Row>
          </FormItem>
          <FormItem>
            <Button type="primary" @click="handleSubmit('formCustom')" style="width: 100%">提交</Button>
          </FormItem>
        </Form>
      </div>
    </div>
  </div>
</template>
<script>
  import HeaderReturn from 'components/header-return/header-resturn.vue'
  import axios from 'axios'
  import { mapActions } from 'vuex'

  export default {
    data () {
      const validateName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入名字！'))
        } else if (!this.isChinaName(value)) {
          callback(new Error('请输入正确的名字！'))
        } else {
          callback()
        }
      }
      const validateIdentity = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入身份证！'))
        } else if (!this.isCardNo(value)) {
          callback(new Error('请输入正确的身份证！'))
        } else {
          callback()
        }
      }
      const validatePhone = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入手机号！'))
        } else if (!this.isPhoneNo(value)) {
          callback(new Error('请输入正确的手机号！'))
        } else {
          this.checkPhone(value).then(res => {
            if (res.data.status === 0) {
              callback()
            } else {
              callback(new Error('该手机号已注册！'))
            }
            this.isChecked = false
          }).catch(err => {
            console.log(err)
            callback(new Error('请检查网络'))
          })
        }
      }
      const validateCode = (rule, value, callback) => {
        if (!value) {
          return callback(new Error('请输入验证码！'))
        } else {
          this.checkCode(value).then(res => {
            if (res.data.status === 0) {
              this.isChecked = true
              return callback()
            } else {
              this.isChecked = false
              return callback(new Error('验证码错误！'))
            }
          }).catch(err => {
            console.log(err)
            this.isChecked = false
            return callback(new Error('请检查网络！'))
          })
        }
      }

      return {
        wait: 60,
        isChecked: false,
        formCustom: {
          name: '',
          identity: '',
          phone: '',
          code: ''
        },
        ruleCustom: {
          name: [
            {validator: validateName, trigger: 'blur'}
          ],
          identity: [
            {validator: validateIdentity, trigger: 'blur'}
          ],
          phone: [
            {validator: validatePhone, trigger: 'blur'}
          ],
          code: [
            {validator: validateCode, trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            axios.post('/users/create', this.formCustom).then(response => {
              let res = response.data
              let user = res.result
              this.setUser({
                name: user.userName,
                phone: user.phone,
                identity: user.identity
              })
            }).catch(err => {
              console.log(err)
              this.$Message.error('请检查网络！')
            })
          } else {
            this.$Message.error('请完善信息！')
          }
        })
      },
      sendSms () {
        let self = this
        axios.post('/sms/send', {
          PhoneNumbers: this.formCustom.phone
        }).then(function (response) {
          let res = response.data
          if (res.status === 0) {
            self.refreshTime()
          } else {
            console.log(response)
          }
        }).catch(function (error) {
          console.log(error)
        })
      },
      refreshTime () {
        let btn = this.$refs.btn
        if (this.wait === 0) {
          btn.removeAttribute('disabled')
          btn.value = '获取验证码'
          btn.style.color = '#333'
          this.wait = 60
        } else {
          btn.setAttribute('disabled', true)
          btn.style.color = '#888'
          btn.value = '重新发送(' + this.wait + ')'
          this.wait--
          setTimeout(() => {
            this.refreshTime()
          }, 1000)
        }
      },
      isChinaName (name) {
        let pattern = /^[\u4E00-\u9FA5]{1,6}$/
        return pattern.test(name)
      },
      isPhoneNo (phone) {
        let pattern = /^1[34578]\d{9}$/
        return pattern.test(phone)
      },
      isCardNo (card) {
        let pattern = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
        return pattern.test(card)
      },
      checkCode (value) {
        return axios.post('/sms/checkAuthCode', {
          phone: this.formCustom.phone,
          authCode: value
        })
      },
      checkPhone (phone) {
        return axios.post('/sms/checkPhone', {
          phone: phone
        })
      },
      ...mapActions(['setUser'])
    },
    activated () {
      //清空表格恢复默认状态
      this.$refs['formCustom'].resetFields()
      this.isChecked = false
    },
    deactivated () {
      //离开后清楚验证码
      axios.post('/sms/deletePhone', {
        phone: this.formCustom.phone
      })
    },
    components: {
      HeaderReturn
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .form-wrapper
    position: fixed
    width: 100%
    top 49px
    bottom 0
    .form-contents
      height 100%
      overflow hidden
      padding-top 30px
      margin-right 30px
      .code
        padding 0 10px
        color $color-text-d
</style>



